import { useState } from "react";
import { useRouter } from "next/router";
import axios from "axios";

export default function Auth() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [isLogin, setIsLogin] = useState(true);
  const router = useRouter();

  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = isLogin ? "/api/auth/login" : "/api/auth/register";
    try {
      const res = await axios.post(`http://localhost:3001${url}`, { username, password });
      if (isLogin) {
        localStorage.setItem("token", res.data.token);
        router.push("/");
      } else {
        alert("ثبت‌نام موفق بود! حالا لاگین کنید.");
        setIsLogin(true);
      }
    } catch (err) {
      alert("مشکلی پیش آمد!");
    }
  };

  return (
    <div className="flex justify-center items-center h-screen bg-gray-200">
      <div className="bg-white p-8 rounded-lg shadow-md w-80">
        <h2 className="text-2xl mb-4">{isLogin ? "ورود" : "ثبت‌نام"}</h2>
        <form onSubmit={handleSubmit}>
          <input type="text" placeholder="نام کاربری" value={username} onChange={(e) => setUsername(e.target.value)}
            className="w-full p-2 mb-2 border rounded"/>
          <input type="password" placeholder="رمز عبور" value={password} onChange={(e) => setPassword(e.target.value)}
            className="w-full p-2 mb-2 border rounded"/>
          <button type="submit" className="w-full bg-blue-500 text-white p-2 rounded">
            {isLogin ? "ورود" : "ثبت‌نام"}
          </button>
        </form>
        <button onClick={() => setIsLogin(!isLogin)} className="text-blue-500 mt-2">
          {isLogin ? "حساب ندارید؟ ثبت‌نام کنید" : "قبلاً ثبت‌نام کرده‌اید؟ ورود"}
        </button>
      </div>
    </div>
  );
}
